import uniGoodsNav from '@/components/uni-goods-nav/uni-goods-nav.vue';

export default {
	data() {
		return {
			id: 0,
			options: [
				{
					icon: 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/uni-ui/goodsnav/kefu.png',
					text: '客服'
				},
				{
					icon: 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/uni-ui/goodsnav/dianpu.png',
					text: '店铺'
				},
				{
					icon: 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/uni-ui/goodsnav/carts.png',
					text: '购物车',
					info: 2
				}
			],
			buttonGroup: [
				{
					text: '加入购物车',
					backgroundColor: '#ff0000',
					color: '#fff'
				},
				{
					text: '立即购买',
					backgroundColor: '#ffa200',
					color: '#fff'
				}
			],
			swipers: [
				{
					src: '../../static/img/banner1.c0f49551.jpg'
				},
				{
					src: '../../static/img/banner2.b5e49622.jpg'
				}
			],
			info: {
				id: 1,
				title: '苹果Apple Iphone 8 Plus 16g 4g手机(三网版)',
				add_time: '2020-03-19',
				goods_no: 'SD12345678',
				stock_quantity: 200,
				market_price: 6000,
				sell_price: 5000
			},
			content: '这是一大段内容'
		};
	},
	onLoad(options) {
		console.log(options);
		this.id = options.id;
		// this.getSwipers()
		// this.getDetailInfo()
	},
	methods: {
		// 获取轮播图数据
		async getSwipers() {
			const res = await this.$myRequest({
				url: '/carousel/carousel' + this.id
			});
			console.log(res.data.data);
			this.swipers = res.data.data.records;
		},
		// 获取详情页数据
		async getDetailInfo() {
			const res = await this.$myRequest({
				url: '/api/goods/getinfo' + this.id
			});
			console.log(res.data.data);
			this.info = res.data.data.records[0];
		},
		// 获取详情页内容数据
		async getDetailContent() {
			const res = await this.$myRequest({
				url: '/api/goods/getdesc' + this.id
			});
			console.log(res.data.data);
			this.content = res.data.data.records[0].content;
		},
		// 导航
		onClick(e) {
			uni.showToast({
				title: `点击${e.content.text}`,
				icon: 'none'
			});
		},
		buttonClick(e) {
			console.log(e);
			this.options[2].info++;
		}
	},
	components: { uniGoodsNav }
};
